header {
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 40px 0;
  z-index: 1000;
}

@media (max-width: 420px) {
  header {
    padding: 20px 6.25%;
  }
}

header a {
  color: #fff;
  transition: 0.3s;
}

header.js-color a {
  color: #020b16;
}

header.js-color .menuIcon:after, header.js-color .menuIcon:before {
  background: #020b16;
}

header .icons a {
  display: block;
  margin-right: 3.2rem;
  font-size: 2.4rem;
  letter-spacing: 0.1em;
}

