  font-size: 2.4rem;
  letter-spacing: 0.1em;
  position: relative;
  font-family: 'futura_r';
  z-index: 1000;
}

header .name:hover {
  opacity: .8;
  color: #fff;
}

@media (max-width: 420px) {
  header .name {
    font-size: 1.6rem;
  }
}

header .container {
  display: flex;
  position: relative;
}

.fa,
.navList a,
.name,
.icons {
  color: #fff;
}

.fa {
  margin-left: 2rem;
  display: inline-block;
  font-size: 2rem;
}

.icons {
  display: flex;
  margin-right: 6.4rem;
}

@media screen and (max-width: 420px) {
  .icons {
    display: none;
  }
}

.menuIcon {
  cursor: pointer;
  width: 50px;
  height: 22px;
  position: relative;
}

@media screen and (max-width: 420px) {
  .menuIcon {
    width: 25px;
    height: 15px;
  }
}

.menuIcon.js-menuOpen:before {
  bottom: 50%;
  transform: rotate(-45deg) translate3d(0, 50%, 0);
}

.menuIcon.js-menuOpen:after {
  top: 50%;
  transform: rotate(45deg) translate3d(0, -50%, 0);
}

.menuIcon:after, .menuIcon:before {
  content: "";
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  right: 0;
  transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.menuIcon:before {
  bottom: 0;
}

.menuIcon:after {
  top: 0;
}

header .navigation {
  background-image: linear-gradient(to right, #072142, #8c2b7a 42%, #ff4d5a);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transform: translate3d(0, -100%, 0);
  z-index: -100;
}

header .navigation.js-open {
  transform: translate3d(0, 0, 0);
}

header .navigation.js-open a {
  transform: translate3d(0, 0, 0);
}

header .navList {
  text-align: center;
  color: #fff;
}

